























































































import Vue from 'vue'
import { getList, deleteRole, createRole } from '@/api/role'
type Form = {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  id: any
  code: string
  name: string
  description: string
}
export default Vue.extend({
  name: 'Role',
  data () {
    return {
      total: 0,
      list: [],
      typeList: [],
      loading: false,
      searchParams: {
        name: '',
        startCreateTime: '',
        code: '',
        endCreateTime: '',
        current: 1,
        size: 10,
      },
      opeaStatus: 'add',
      show: false,
      form: {
        id: '',
        code: '',
        name: '',
        description: ''
      }
    }
  },
  created () {
    this.getList()
  },
  methods: {
    async getList () {
      this.loading = true
      try {
        const res = await getList(this.searchParams)
        this.list = res.data.data.records
        this.total = res.data.data.total
      } catch (error) {
        this.$message.error(error.message)
      } finally {
        this.loading = false
      }
    },
    createDialog () {
      this.opeaStatus = 'add'
      this.show = true
    },
    editDialog ({ id, name, code, description }: Form) {
      this.opeaStatus = 'edit'
      this.show = true
      this.form = {
        id, name, code, description
      }
    },
    async createRole () {
      this.loading = true
      try {
        await createRole(this.form)
        this.$message.success('创建成功')
        this.show = false
        this.getList()
      } catch (error) {
        this.$message.error(error.message)
      } finally {
        this.loading = false
      }
    },
    goResClassify () {
      this.$router.push({
        name: 'resource-type',
      })
    },
    async deleteRes (id: number) {
      await deleteRole(id)
      this.getList()
    },
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    allocateMenu (id: any) {
      this.$router.push({
        name: 'allocate-menu',
        params: {
          roleId: id,
        }
      })
    },
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    allocateRes (id: any) {
      this.$router.push({
        name: 'allocate-res',
        params: {
          roleId: id,
        }
      })
    },
    allocateRoleRes () {
      console.log('allocateRoleRes')
    }
  },
})
