



































































import Vue from 'vue'
import { getList, deleteRes, getResTypeList } from '@/api/resource'
// import dayjs from 'dayjs'
export default Vue.extend({
  name: 'Resource',
  data () {
    return {
      total: 0,
      list: [],
      typeList: [],
      loading: false,
      searchParams: {
        name: '',
        startCreateTime: '',
        url: '',
        categoryId: '',
        endCreateTime: '',
        current: 1,
        size: 10,
      },
    }
  },
  created () {
    this.getTypeList()
    this.getList()
  },
  methods: {
    async getTypeList () {
      const res = await getResTypeList()
      this.typeList = res.data.data
    },
    async getList () {
      this.loading = true
      try {
        const res = await getList(this.searchParams)
        this.list = res.data.data.records
        this.total = res.data.data.total
      } catch (error) {
        this.$message.error(error.message)
      } finally {
        this.loading = false
      }
    },
    create () {
      this.$router.push({
        name: 'resource-create',
      })
    },
    edit (id: string) {
      this.$router.push({
        name: 'resource-create',
        query: {
          id,
        },
      })
    },
    goResClassify () {
      this.$router.push({
        name: 'resource-type',
      })
    },
    async deleteRes (id: number) {
      await deleteRes(id)
      this.getList()
    },
  },
})
