






































import Vue from 'vue'
import { getList, deleteMenu } from '@/api/menu'
export default Vue.extend({
  name: 'Menu',
  data () {
    return {
      loading: false,
      list: []
    }
  },
  created () {
    this.getList()
  },
  methods: {
    async getList () {
      this.loading = true
      try {
        const res = await getList()
        this.list = res.data.data
      } catch (error) {
        this.$message.error(error.message)
      } finally {
        this.loading = false
      }
    },
    create () {
      this.$router.push({
        name: 'menu-create'
      })
    },
    edit (id: string) {
      this.$router.push({
        name: 'menu-create',
        query: {
          id
        }
      })
    },
    async deleteMenu (id: number) {
      await deleteMenu(id)
      this.getList()
    }
  }
})
