















































































import Vue from 'vue'
import { getList } from '@/api/user'
import { getAllRole, allocateUserRoles, getUserRole } from '@/api/role'
import dayjs from 'dayjs'
export default Vue.extend({
  name: 'User',
  data () {
    return {
      dayjs: dayjs,
      loading: false,
      total: 0,
      list: [],
      roleList: [],
      show: false,
      searchParams: {
        phone: '',
        startCreateTime: '',
        code: '',
        endCreateTime: '',
        current: 1,
        size: 10,
      },
      form: {
        roleIdList: [],
        userId: ''
      }
    }
  },
  created () {
    this.getAllRole()
    this.getList(this.searchParams)
  },
  methods: {
    search () {
      this.getList(this.searchParams)
    },
    async getList (searchParams: object) {
      this.loading = true
      try {
        const res = await getList(searchParams)
        this.list = res.data.data.records
        this.total = res.data.data.total
      } catch (error) {
        this.$message.error(error.message)
      } finally {
        this.loading = false
      }
    },
    async allocateRole () {
      try {
        await allocateUserRoles(this.form)
        this.$message.success('分配成功')
        this.show = false
        this.getList(this.searchParams)
      } catch (error) {
        console.log(error)
      }
    },
    async getAllRole () {
      const { data } = await getAllRole()
      this.roleList = data.data
    },
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    allocateRoleDialog (id: any) {
      this.show = true
      this.form.userId = id
      this.getUserRole(id)
    },
    async getUserRole (userId: string|number) {
      const { data } = await getUserRole(userId)
      console.log(data)
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      this.form.roleIdList = data.data.map((item: any) => item.id)
    }
  }
})
